<template>
  <Layout :style="{ height: height + 'px','overflow': 'hidden','flex-direction':'row'}">
    <div class="left-sider">
      <sider-menu
        :active-name="activeName"
        @on-select="turnTopage"
        :sider-menulist="siderMenuList"
        :sub-menulist="subMenuList"
        accordion
        ref="sideMenu"
      ></sider-menu>
    </div>
    <Layout :style="{ height: height + 'px' }" class="right-layout">
      <router-view ></router-view>
    </Layout>
  </Layout>
</template>
<script>
import siderMenu from "@common/sider-menu";
import { siderMenuList1 } from "@utils/data.js";
export default {
  name: "index",
  components: {
    siderMenu,
  },
  data() {
    return {
      height: 0,
      activeName: "",
      siderMenuList: siderMenuList1,
      subMenuList: [],
    };
  },
  // watch: {
  //   $route(to, from) {
  //     this.getAvtivename();
  //   }
  // },
  methods: {
    setHeight() {
      this.height = document.getElementById("app").clientHeight - 80;
      // console.log(this.height);
    },
    handleResize() {
      // console.log("resize");
      this.$nextTick(() => {
        this.setHeight();
      });
    },
    turnTopage() {},
    getAvtivename() {
        // console.log('getAvtivename',this.$route.name)
        this.activeName = this.$route.name
    }
  },
  mounted() {
    // console.log('mounted')
    window.addEventListener(
      "resize",
      this._.debounce(this.handleResize, 100, false)
    );
    this.$nextTick(() => {
      this.setHeight();
    });
    this.getAvtivename();
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.handleResize, false);
  }
};
</script>
<style lang="scss" scoped>
@import "@style/index.scss";

::v-deep .ivu-menu {
  // background: #f3f7fa;
  margin-right: -25px; //icon
  > .ivu-menu-item {
    padding: 18px 56px 18px 60px !important;
    &:hover {
      > i {
        color: $main-color;
      }
      > span {
        color: $main-color;
      }
    }
    > i {
      @include font(#576981, 24px);
      margin-right: 0;
    }
    > span {
      display: inline-block;
      @include font($title-color, $font-second);
      margin-left: 8px;
      font-weight: 550;
      text-align: left;
      vertical-align: 0.15em;
      width: 98px;
    }
    &.ivu-menu-item-active:not(.ivu-menu-submenu) {
      > i {
        color: $main-color;
      }
      > span {
        color: $main-color;
      }
      background: transparent;
      &::after {
        width: 0;
      }
    }
  }
}
</style>