import Vue from 'vue';
import Component from 'vue-class-component';
@Component({
  props: {
    activeName: {
      type: String,
      default: ""
    },
    siderMenulist: {
      type: Array,
      default: () => []
    },
    subMenulist: {
      type: Array,
      default: () => []
    },
    contextMenu: {
      type: Boolean,
      default: false
    },
    accordion: {
      type: Boolean,
      default: false
    },
    openedNames: {
      type: Array,
      default: () => []
    },
  },
  watch: {
    openedNames(value) {
      this.$nextTick(() => {
        this.$refs.menu.updateOpened();
      });
    }
  },
})
export default class siderMenu extends Vue {
  openMenu(e, data) {
    this.$emit('open-menu', e, data);
  }
  submenuChange(name) {
    console.log('sdasaaaaaaaaaaaaaaaaaa')
    this.$emit('submenu-change', name,false);
  }
}
