import Vue from 'vue'
import VueRouter from 'vue-router'
import mainBody from '@views/mainBody.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    redirect: '/dispatchManage',
    component: mainBody,
    children: [{
      path:'/dispatchManage',
      name: 'dispatchManage', 
      component: () => import('../views/dataCompilate/messageManage/dispatchManage.vue')
    }, {
      path:'/workManage',
      name: 'workManage', 
      component: () => import('../views/dataCompilate/workManage/workManage.vue')
    }, {
      path:'/dataSource',
      name: 'dataSource', 
      component: () => import('../views/dataCompilate/dataSource/systemManage.vue')
    }, {
      path:'/templateChange', 
      name: 'templateChange', 
      component: () => import('../views/dataCompilate/templateChange/templateChange.vue')
    }]
  },
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
