<template>
  <div class="slider-menu-wrapper">
    <Menu
      ref="menu"
      :active-name="activeName"
      width="auto"
      :accordion="accordion"
      :open-names="openedNames"
      v-if="!_.isEmpty(subMenulist)"
      @on-open-change="submenuChange"  
    >
      <template v-for="item of siderMenulist">
        <Submenu :name="item.id" :key="item.id">
          <template slot="title" v-if="contextMenu">
            <div
              :title="item.name"
              class="menu-title"
              @contextmenu.prevent="openMenu($event, item.row)"
            >
              <span class="icon-wapper">
                <Icon :custom="'sjzl-icon sjzl-'+ item.icon"></Icon>
              </span>
              <span class="title-name">{{item.name}}</span>
            </div>
          </template>
          <template slot="title" v-else>
            <div :title="item.name" class="menu-title">
              <span class="icon-wapper">
                <Icon :custom="'sjzl-icon sjzl-'+ item.icon"></Icon>
              </span>
              <span class="title-name">{{item.name}}</span>
            </div>
          </template>
          <template v-for="subitem of subMenulist">
            <MenuItem
              :name="item.id +'-'+ subitem.type"
              :key="subitem.id"
              :to="{ path:subitem.route, query: { id: item.id ,type:subitem.type}}"
            >
              <Icon :custom="'sjzl-icon sjzl-'+ subitem.icon"></Icon>
              <span>{{subitem.name}}</span>
            </MenuItem>
          </template>
        </Submenu>
      </template>
    </Menu>
    <Menu :active-name="activeName"  ref="menu" v-else>
      <template v-for="item of siderMenulist">
        <MenuItem :name="item.route" :key="item.id" :to="item.route">
          <Icon :custom="'sjzl-icon sjzl-'+ item.icon"></Icon>
          <span>{{item.name}}</span>
        </MenuItem>
      </template>
    </Menu>
    <slot name="contextMenu"></slot>
  </div>
</template>
<script>
import siderMenu  from "./sider-menu.class";
export default siderMenu
</script>
<style lang="scss" scoped>
@import "./index.scss";
</style>