import { get, post, download} from "./axios";

const URL_TEMPLATE = '/template/templateInfo'
const URL_DATABASEINFO = '/template/databaseInfo'
const URL_INDENTIFER = '/template/identifierMappingInfo'
const URL_FILEOPERATE = '/template/fileOperate'
const URL_DIANWEN = '/template/textualInfo'
const URL_SQL = '/template/sqlExecute'
const CHONG = 'http://127.0.0.1:8081'

export const api = {
  ADD_DB_SOURCE:  (api, params) =>  post(`${CHONG}${URL_DATABASEINFO}${api}`, params),
  GET_ALL: (api, params) => get(`${CHONG}${URL_DATABASEINFO}${api}`, params),
  DELETE_ID: (api, params) => post(`${CHONG}${URL_DATABASEINFO}${api}`, params),
  LIST: (api, params) => get(`${CHONG}${URL_DATABASEINFO}${api}`, params),
  LIST_DB_SOURCE_TABLES: (api, params) => post(`${CHONG}${URL_DATABASEINFO}${api}`, params),
  LIST_TABLE_COLUMN: (api, params) => post(`${CHONG}${URL_DATABASEINFO}${api}`, params),
  DbSourceNameLike: (api, params) => post(`${CHONG}${URL_DATABASEINFO}${api}`, params),

  
  FIND_SQL:(api, params) => post(`${CHONG}${URL_INDENTIFER}${api}`, params),
  UPDATE_SQL: (api, params) => post(`${CHONG}${URL_INDENTIFER}${api}`, params),
  
  CREATE_WORD: (api, params) => download(`${CHONG}${URL_FILEOPERATE}${api}`, params),
  
  TEXT_FIND_ALL: (api, params) => get(`${CHONG}${URL_DIANWEN}${api}`, params),
  TEXT_DOWNLOAD: (api, params) => download(`${CHONG}${URL_DIANWEN}${api}`, params),
  TextFindByName: (api, params) => post(`${CHONG}${URL_DIANWEN}${api}`, params), 
  
  DBSOURCE_ID: (api, params) => post(`${CHONG}${URL_TEMPLATE}${api}`, params),
  UP_DATA_SOURCE: (api, params) => post(`${CHONG}${URL_TEMPLATE}${api}`, params),
  FIND_ALL: (api, params) => get(`${CHONG}${URL_TEMPLATE}${api}`, params),
  TEM_DELETE: (api, params) => post(`${CHONG}${URL_TEMPLATE}${api}`, params),
  TemFindByName: (api, params) => post(`${CHONG}${URL_TEMPLATE}${api}`, params),
  
  SQL_QUERY: (api, params) => post(`${CHONG}${URL_SQL}${api}`, params)
}