import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    databaseList: [],
    templateMsg: ''
  },
  mutations: {
    saveDatabaseList: function (state, databaseList) {
      state.databaseList = databaseList
    },
    saveTemplateMsg: function(state, templateMsg) {
      state.templateMsg = templateMsg
    }
  },
  actions: {
  },
  modules: {
  }
})
